import { Component, OnInit } from '@angular/core';
import { Services } from '../Global/Services';
import { Router } from '@angular/router';
import { DomSanitizer } from '@angular/platform-browser';


@Component({
  selector: 'app-view',
  templateUrl: './view.component.html',
  styleUrls: ['./view.component.scss']
})
export class ViewComponent implements OnInit {
  fir: any;
  CrimeNumber = "";
  InformationConveyedTo = "";
  viewCaseData: any;
  LocationsData: any = [];
  locationsarr = [];
  lat: number = 15.9129;
  lng: number = 79.7400;
  lat1: any;
  lng1: any;
  zoom = 12;
  isFirRegistered = null;
  textMsg = "";
  errMsg = "";
  url: any;
  sanitizedUrl: any;
  constructor(private _Services: Services, private _Router: Router, private sanitizer: DomSanitizer) {
    console.log(_Services.ViewCaseDetails);

    this.viewCaseData = _Services.ViewCaseDetails;

    this.sanitizedUrl = this.sanitizer.bypassSecurityTrustUrl('http://maps.google.com/?q=' + this.viewCaseData.latitude + ',' + this.viewCaseData.longitude);
    var obj = {
      "sosId": this.viewCaseData._id,
      "sosSeenBy": "admin"
    }
    this._Services.postAPI("sosServices/sosSeen", obj).subscribe(res => {
      console.log(res);
    }, err => {
      console.log(err);
    });

    this.lat1 = parseFloat(this._Services.ViewCaseDetails.latitude);
    this.lng1 = parseFloat(this._Services.ViewCaseDetails.longitude);
    var inputObj = {
      "name": "policestations",
      "distance": 50000,
      "coordinates": [parseFloat(this._Services.ViewCaseDetails.longitude), parseFloat(this._Services.ViewCaseDetails.latitude)]
    }
    console.log(inputObj);
    this.getData(inputObj);
  }

  ngOnInit() {
  }

  getData(inputObj) {
    this._Services.postAPI("getLocations", inputObj).subscribe(res => {
      console.log(res["data"]);
      this.LocationsData = res["data"];
      if (this.LocationsData.length > 0) {
        for (var i = 0; i < this.LocationsData.length; i++) {
          this.locationsarr.push({ psName: this.LocationsData[i].psName, psMobile: this.LocationsData[i].mobile, psLandline: this.LocationsData[i].landLine, lng: this.LocationsData[i].location.coordinates[0], lat: this.LocationsData[i].location.coordinates[1], psUnit: this.LocationsData[i].unit, });
        }
        console.log(this.locationsarr);
      }
      //this.LocationsData = new MatTableDataSource(this.LocationsData);
      // setTimeout(() => {
      //   this.LocationsData.paginator= this.paginator;
      // }, 0);
      // console.log(this.LocationsData);

    }, err => {
      console.log(err);
    });
  }


  GoToAssignedPage() {
    this._Router.navigate(["/Homepage/Assignedpage"]);
  }

  getFIRvalue(inputText) {
    console.log(inputText);
    this.isFirRegistered = inputText;
  }

  completeCase(textMsg) {
    this.errMsg = "";
    console.log(textMsg);
    if (textMsg != "" && this.isFirRegistered != null) {
      this.errMsg = "";
      var obj = {
        "sosId": this.viewCaseData._id,
        "sosCompletedBy": this.viewCaseData.sosAssignedDetails.sosAssignedBy,
        "sosCompletedReport": {
          "policeStationId": this.viewCaseData.sosAssignedDetails.sosAssignedTo.policeStationId,
          "policeStationName": this.viewCaseData.sosAssignedDetails.sosAssignedTo.policeStationName,
          "policeStationArea": this.viewCaseData.sosAssignedDetails.sosAssignedTo.policeStationArea,
          "closingReason": textMsg,
          "CrimeNumber": this.CrimeNumber,
          "InformationConveyedTo": this.InformationConveyedTo
        },
        "firRegistered": this.isFirRegistered
      }
      console.log(obj);
      this._Services.postAPI("sosServices/sosCompleted", obj).subscribe(res => {
        console.log(res);
        this._Services.DashBoardTitle = "Completed";
        this._Router.navigate(["/Homepage/Sos"]);
      }, err => {
        console.log(err);
      });
    } else {
      this.errMsg = "Please Enter Closing Reason and FIR Status";
    }

  }
  completeCase1(textMsg) {
    this.errMsg = "";
    console.log(textMsg);
    if (textMsg != "" ) {
      this.errMsg = "";
      var obj = {
        "sosId": this.viewCaseData._id,
        "sosCompletedBy": "",
        "sosCompletedReport": {
          "policeStationId": "",
          "policeStationName": "",
          "policeStationArea": "",
          "closingReason": textMsg,
          "CrimeNumber": this.CrimeNumber,
          "InformationConveyedTo": this.InformationConveyedTo
        },
        "firRegistered": this.isFirRegistered
      }
      console.log(obj);
      this._Services.postAPI("sosServices/sosCompleted", obj).subscribe(res => {
        console.log(res);
        this._Services.DashBoardTitle = "Completed";
        this._Router.navigate(["/Homepage/Sos"]);
      }, err => {
        console.log(err);
      });
    } else {
      this.errMsg = "Please Enter Closing Reason and FIR Status";
    }

  }


  onMouseOver(infoWindow, gm) {
    console.log(gm);
    if (gm.lastOpen && gm.lastOpen.isOpen) {
      gm.lastOpen.close();
    }

    gm.lastOpen = infoWindow;

    infoWindow.open();
  }

  onMouseOut(infoWindow, $event: MouseEvent) {
    infoWindow.close();
  }

}
