import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomepageComponent } from './homepage/homepage.component';
import { LoginComponent } from './login/login.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { ViewComponent } from './view/view.component';
import { AssignedpageComponent } from './assignedpage/assignedpage.component';
import { TrackMyTravelComponent } from './track-my-travel/track-my-travel.component';
import { SosComponent } from './sos/sos.component';
import { ViewTrackComponent } from './view-track/view-track.component';
import { UserProfileComponent } from './user-profile/user-profile.component';
import { ReportsComponent } from './reports/reports.component';
import { ReportViewComponent } from './report-view/report-view.component';
import { ChangePasswordComponent } from './change-password/change-password.component';
import { AllReportsComponent } from './all-reports/all-reports.component';
import { AllReportsViewComponent } from './all-reports-view/all-reports-view.component';




const routes: Routes = [
  { path: '', redirectTo: 'Login', pathMatch: 'full' },
  { path: 'Login', component: LoginComponent },
  { path: 'Homepage', component: HomepageComponent,
    children: [
      { path: '', redirectTo: 'Dashboard', pathMatch: 'full' },
      { path: 'Dashboard', component: DashboardComponent },
      { path: 'View/:Id', component: ViewComponent },
      { path: 'Assignedpage', component: AssignedpageComponent },
      { path: 'TrackMyTravel', component: TrackMyTravelComponent },
      { path: 'ViewTrack', component: ViewTrackComponent },
      { path: 'Sos', component: SosComponent },
      { path: 'UserProfile', component: UserProfileComponent },
      { path: 'Reports', component: ReportsComponent },
      { path: 'ReportView', component: ReportViewComponent },
      { path: 'ChangePassword', component: ChangePasswordComponent },
      { path: 'AllReports', component: AllReportsComponent },
      { path: 'AllReportsView', component: AllReportsViewComponent }


    ]
  },
  
  // { path: '', component: HomepageComponent },
  // { path: 'Login', component: LoginComponent },
  // { path: 'Homepage', component: HomepageComponent },
  //{ path: 'products/:productId', component: ProductDetailsComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes,{useHash: true})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
