import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Services } from '../Global/Services';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  errorMsg="";
  UserData = {
    userName: "",
    password: ""
  }
  constructor(private _Router:Router,private _Services:Services) { }

  ngOnInit() {
  }

  loginUser(inputObj) {
    console.log(inputObj);
    if (inputObj.userName != "" && inputObj.password != "") {
      this._Services.postAPI("admin/login",inputObj).subscribe(res => {
        console.log(res);
      this.errorMsg="";
      this._Services.UserData=res["data"].adminDetails;
      localStorage.setItem('UserDetails', JSON.stringify(this._Services.UserData));
      console.log(JSON.parse(localStorage.getItem('UserDetails')));
      //localStorage.getItem('key');
      var obj={
        "district": this._Services.UserData.district
      }   
      this._Services.postAPI("getTripCount",obj).subscribe(tripCounts => {
        console.log(tripCounts["data"]);
        this._Services.TripsCount=tripCounts["data"];
      },err=>{
        console.log(err);
      });
      this._Services.postAPI("allsosRecordsCount",obj).subscribe(sosCounts => {
        console.log(sosCounts["data"]);
        this._Services.SOSCount=sosCounts["data"];
      this._Router.navigate(["/Homepage"]);

           },err=>{
             console.log(err);
           });

      // this._Router.navigate(["/Homepage"]);
      }, err => {
        console.log(err);
      });

    }else{
      this.errorMsg="Please enter valid details";
    }
  }

  onSearchChange(event,inputObj){
console.log(event);
if(event.keyCode==13){
this.loginUser(inputObj);
}
  }
}
