  postAPI(url, inputObj) {
    return this.http
      .post(this.BaseUrl + url, inputObj)
      .map((res) => {
        return res;
      });
  }

  getAPIfun(url) {
    return this.http
      .get(this.BaseUrl + url)
      .map((res) => {
        return res;
      });
  }

