import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { Services } from '../Global/Services';
import { DatePipe } from '@angular/common';
import { Router } from '@angular/router';
import 'rxjs/add/observable/interval';
import { interval } from 'rxjs';
import { Howl } from 'howler';

@Component({
  selector: 'app-homepage',
  templateUrl: './homepage.component.html',
  styleUrls: ['./homepage.component.scss'],
  providers: [DatePipe]
})
export class HomepageComponent implements OnInit {
  getCount: any;
  unseenCount: any;
  getase: any;
  getTrip:any;
  caseCount: any;
  tripCount:any;
  alertMessage = "";
  alertMessage1 = "";

//   _id: "5e5b7f90e3c0ec6149afed62"
// userName: "testUser1D"
// createdAt: "2020-03-01T09:25:36.739Z"
// mobileNumber: "8801108108"
// email: "NA"
// district: "Kishna"
// timestamp: "2020-03-01T09:25:36.739Z"


  constructor(private _Router: Router, private _Services: Services) {
    var obj={
      "district": this._Services.UserData.district
    } 
    // console.log(this._Services.UserData.userName);
    // this._Services.getAPIfun("sosServices/pending").subscribe(res => {
    //   //console.log(responseObj["data"].unseenSosCount);
    //   this.caseCount = res["data"].length;
    // }, err => {
    //   console.log(err);
    // });

    // this._Services.getAPIfun("getTrips/active").subscribe(res => {
    //   //console.log(responseObj["data"].unseenSosCount);
    //   this.tripCount = res["data"].length;
    // }, err => {
    //   console.log(err);
    // });

    //notification count
    this.getCount = interval(3000).subscribe((val) => { 
      this._Services.postAPI("sosServices/unseenSosCount/district",obj).subscribe(responseObj => {
      //console.log('called');
      // this._Services.getAPIfun("sosServices/unseenSosCount").subscribe(responseObj => {
        //console.log(responseObj["data"].unseenSosCount);
        this.unseenCount = responseObj["data"].unseenSosCount;
      }, err => {
        console.log(err);
      });


    });

    //pending case
    this.getase = interval(3000).subscribe((val) => {
      //console.log('called');
      // this._Services.getAPIfun("sosServices/pending").subscribe(res => {
        this._Services.postAPI("allsosRecordsCount",obj).subscribe(sosCounts => {
          console.log(sosCounts["data"]);
        //console.log(this.caseCount , res["data"].length);
        if (this._Services.SOSCount.pendingSOSCount < sosCounts["data"].pendingSOSCount) {
          this.alertMessage = "SOS has been Raised";
          let sound = new Howl({
            src: ['disha/assets/sos.mp3'],
    
          });
          sound.play();
          this._Services.SOSCount.pendingSOSCount = sosCounts["data"].pendingSOSCount;
          setTimeout(() => {
            this.alertMessage = "";
          }, 2500);
        } else {
          this.alertMessage = "";
        }
      }, err => {
        console.log(err);
      });
    });

//trip
    this.getTrip = interval(3000).subscribe((val) => {
      //console.log('called');
      // this._Services.getAPIfun("getTrips/active").subscribe(res => {
        this._Services.postAPI("getTripCount",obj).subscribe(tripCounts => {
          console.log(tripCounts["data"]);
        //console.log(this.caseCount , res["data"].length);
        if (this._Services.TripsCount.activeTripsCount < tripCounts["data"].activeTripsCount) {
          this.alertMessage1 = "New Trip Added";
          let sound = new Howl({
            src: ['../assets/sos.mp3']
          });
          sound.play();
          this._Services.TripsCount.activeTripsCount = tripCounts["data"].activeTripsCount;
          setTimeout(() => {
            this.alertMessage1 = "";
          }, 4500);
        } else {
          this.alertMessage1 = "";
        }
      }, err => {
        console.log(err);
      });
    });

  }

  ngOnInit() {

  }

  changePage(inputData) {
    this._Services.DashBoardTitle = inputData;
    if (inputData == 'Dashboard') {
      this._Router.navigate(["/Homepage/Dashboard"]);
    } else if (inputData == 'TrackMyTravel') {
      this._Router.navigate(["/Homepage/TrackMyTravel"]);
    } else {
      this._Router.navigate(["/Homepage/Sos"]);
    }

  }

  SignOut() {
    localStorage.clear();
    this._Router.navigate(["/Login"]);
  }


  goToUserProfile(){
    this._Router.navigate(["/Homepage/UserProfile"]);
  }

  goToChangePassword(){
    this._Services.DashBoardTitle = "Change Password";
    this._Router.navigate(["/Homepage/ChangePassword"]);
  }
  
  goToReports(){
    this._Services.DashBoardTitle = "Reports";
    this._Router.navigate(["/Homepage/Reports"]);
  }

  goToAllReports(){
    this._Services.DashBoardTitle = "All Reports";
    this._Services.isAllView=false;
    this._Router.navigate(["/Homepage/AllReports"]);
  }
}



