import { Component, OnInit } from '@angular/core';
import { Services } from '../Global/Services';
import 'rxjs/add/observable/interval';
import { interval } from 'rxjs';

@Component({
  selector: 'app-view-track',
  templateUrl: './view-track.component.html',
  styleUrls: ['./view-track.component.scss']
})
export class ViewTrackComponent implements OnInit {
  updateTrack: any;
  lat1 = 0;
  lng1 = 0;
  zoom = 10;
  public lat = 0;
public lng = 0;
 
public origin: any;
public destination: any;

  dir = {
    origin: { lat: 24.799448, lng: 120.979021 },
    destination: { lat: 24.799524, lng: 120.975017 }
  }

  constructor(private _Services: Services) {

    this._Services.postAPI("getTrip/" + this._Services.LocationDetails.tripId, {}).subscribe(res => {
      //this.caseCount = res["data"].length;
      console.log(res["data"]);
      this.lat1 = res["data"][0].lastLocationDetails.latitude;
      this.lng1 = res["data"][0].lastLocationDetails.longitude;
      this.lat = parseFloat(res["data"][0].tripSource.srcLat);
      this.lng = parseFloat(res["data"][0].tripSource.srcLng);

      this.origin = { lat: parseFloat(res["data"][0].tripSource.srcLat), lng: parseFloat(res["data"][0].tripSource.srcLng) };
      this.destination = { lat: parseFloat(res["data"][0].tripDestination.destLat), lng: parseFloat(res["data"][0].tripDestination.destLng) };
      console.log( this.origin,this.destination);
    }, err => {
      console.log(err);
    });

    this.updateTrack = interval(3000).subscribe((val) => {
      this._Services.postAPI("getTrip/" + this._Services.LocationDetails.tripId, {}).subscribe(res => {
        //this.unseenCount = responseObj["data"].unseenSosCount;
        console.log(res["data"][0].lastLocationDetails);
        this.lat1 = res["data"][0].lastLocationDetails.latitude;
        this.lng1 = res["data"][0].lastLocationDetails.longitude;
      }, err => {
        console.log(err);
      });


    });
  }
  
  ngOnInit() {
    //this.getDirection();
  }

  getDirection() {
    this.origin = { lat: 24.799448, lng: 120.979021 };
    this.destination = { lat: 24.799524, lng: 120.975017 };
   
    // this.origin = 'Taipei Main Station';
    // this.destination = 'Taiwan Presidential Office';
  }

}
